<template>
	<view class="tui-collapse" :style="{backgroundColor:bgColor}">
		<view class="tui-collapse-head" :style="{backgroundColor:hdBgColor}" @tap.stop="handleClick">
			<view class="tui-header" :class="{'tui-opacity':disabled}">
				<slot name="title"></slot>
				<view class="tui-all-box tui-icon-ml">
					<view class="tui-attr-right tui-active">{{condition}}</view>
					<view class="tui-icon-arrow-notice" v-if="!isOpen">展开</view>
					<view class="tui-collapse-icon tui-icon-arrow" :class="{'tui-icon-active':isOpen}" :style="{color:arrowColor}" v-if="arrow"></view>
				</view>	
			</view>
		</view>
		<view class="tui-collapse-body_box" :style="{backgroundColor:bdBgColor}">
			<slot name="top"></slot>
			<view class="tui-collapse-body" 
			    :style="{height:isOpen?height:'0rpx'}"
				:class="{'tui-collapse-transform':height=='auto','tui-collapse-body_show':isOpen && height=='auto'}">
				<slot name="content"></slot>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "tuiCollapse",
		emits: ['click'],
		props: {
			//collapse背景颜色
			bgColor: {
				type: String,
				default: 'transparent'
			},
			//collapse-head 背景颜色
			hdBgColor: {
				type: String,
				default: '#fff'
			},
			//collapse-body 背景颜色
			bdBgColor: {
				type: String,
				default: 'transparent'
			},
			condition: {
				type: String,
				default: ''
			},
			//collapse-body实际高度 open时使用
			height: {
				type: String,
				default: 'auto'
			},
			//索引
			index: {
				type: Number,
				default: 0
			},
			//当前索引，index==current时展开
			current: {
				type: Number,
				default: -1
			},
			// 是否禁用
			disabled: {
				type: [Boolean, String],
				default: false
			},
			//是否带箭头
			arrow: {
				type: [Boolean, String],
				default: true
			},
			//箭头颜色
			arrowColor: {
				type: String,
				default: "#333"
			}
		},
		watch: {
			current() {
				this.updateCurrentChange()
			}
		},
		created() {
			this.updateCurrentChange()
		},
		data() {
			return {
				isOpen: false
			};
		},
		methods: {
			updateCurrentChange() {
				this.isOpen = this.index == this.current
			},
			handleClick() {
				if (this.disabled) return;
				this.$emit("click", {
					index: Number(this.index)
				})
			}
		}
	}
</script>

<style scoped>
	@font-face {
		font-family: 'tuiCollapse';
		src: url(data:application/font-woff;charset=utf-8;base64,) format('woff');
		font-weight: normal;
		font-style: normal;
	}

	.tui-collapse-icon {
		font-family: "tuiCollapse" !important;
		font-style: normal;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}

	.tui-icon-arrow:before {
		content: "\e600";
	}

	.tui-icon-arrow {
		font-size: 18rpx;
		transform: rotate(0);
		transform-origin: center center;
		transition: all 0.3s;
		line-height: 30rpx;
	}

	.tui-all-box {
		width: 90%;
		font-size: 20rpx;
		margin-right: 30rpx;
		white-space: nowrap;
		display: flex;
		align-items: center;
		justify-content: flex-end;
	}
	.tui-attr-right {
		width: 70%;
		color: #e41f19;
		white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		text-align: right;
		margin-right: 15rpx;
	}
	.tui-icon-arrow-notice{
		transition: all 0.3s;
		margin-right: 6rpx
	}
	.tui-arrow-padding {
		padding-right: 62rpx;
		box-sizing: border-box;
	}

	.tui-icon-active {
		transform: rotate(180deg);
		transform-origin: center center;
	}

	.tui-header {
		display: flex;
		align-items: center;
		z-index: 2;
	}
   .tui-collapse-body_box{
	   transition: all 0.25s;
	   overflow: hidden;
   }
	.tui-collapse-body {
		transition: all 0.25s;
		overflow: hidden;
		position: relative;
		z-index: 1;
	}

	.tui-collapse-transform {
		opacity: 0;
		visibility: hidden;
		-webkit-transform: translateY(-40%);
		transform: translateY(-40%);
	}

	.tui-collapse-body_show {
		opacity: 1;
		visibility: visible;
		-webkit-transform: translateY(0);
		transform: translateY(0);
	}

	.tui-opacity {
		opacity: 0.6;
	}
</style>
